.consultation {
  background: #d50000;
  padding: 69px 0;

  &__wrapper {
    display: flex;
    align-items: center;
    color: #fff;
    font-size: calc(15px + 15 * (100vw / 1920));
    text-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    gap: 20px;
  }

  &__item {
    flex: 0 1 886px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  }

  &__abovetitle {
    margin-bottom: 25px;
  }

  &__title {
    font-size: calc(25px + 20 * (100vw / 1920));
    margin-bottom: 40px;
  }

  &__subtitle {
    margin-bottom: calc(25px + 45 * (100vw / 1920));
  }

  &__text {
    font-size: calc(9px + 11 * (100vw / 1920));
    text-shadow: none;
  }

  &__form {
    flex: 0 1 591px;
  }
  &__text--mobile {
    display: none;
  }
}

.form {

  &__wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__item-input {
    border: none;
    outline: none;
    width: 100%;
    height: calc(38px + 34 * (100vw / 1920));
    font-weight: 500;
    font-size: calc(15px + 15 * (100vw / 1920));
    padding: 0 42px;
  }
  &__item-input{
    margin-bottom: 23px;
  }
  &__item-input:nth-child(2) {
    margin-bottom: 0;
  }

  &__item-btn {
    position: relative;
    overflow: hidden;
    width: 328px;
    height: calc(37px + 28 * (100vw / 1920));
    background: linear-gradient(
    -90deg,
    #6d0000,
    #6d0000,
    #ffffffcc,
    #6d0000,
    #6d0000
  );
  background-size: 400% 10%;
  animation: Gradient 4s ease infinite;
    // background: #6d0000;
    font-weight: 700;
    font-size: calc(15px + 15 * (100vw / 1920));
    color: #fff;
    margin-top: calc(20px + 27 * (100vw / 1920));
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    cursor: pointer;
  }
}
@keyframes Gradient {
  50% {
    background-position: 140% 50%;
    transform: skew(-2deg);
  }
}

.form__item-btn::after {
  content: "";
  position: absolute;
  background-size: inherit;
  animation: inherit;
  left: 0px;
  right: 0px;
  top: 2px;
  height: 100%;
}

.just-validate-error-label {
  display: none;
}
.just-validate-error-field {
  border: 2px solid #6d0000;
}

@media(max-width: 650px) {
  .consultation__wrapper {
    display: block;
  }
  .consultation__text--mobile {
    display: block;
    margin-top: 10px;
  }
  .consultation__text--desctop{
    display: none;
  }
}

@media(max-width: 540px) {
  .consultation {
    padding: 26.35px 0;
  }
  .consultation__wrapper{
    font-size: 15px;
  }
  .consultation__title{
    font-size: 25px;
  }
  .consultation__abovetitle {
    margin-bottom: 28px;
  }
  .consultation__title{
    margin-bottom: 34px;
  }
  .consultation__subtitle {
    margin-bottom: 26px;
    line-height: 146%;
  }
  .form__item-input {
    width: 100%;
    height: 38px;
    padding: 0 22.67px;
    font-weight: 500;
    font-size: 15px;
  }
  .form__item-input:not(:last-child) {
    margin-bottom: 26.34px;
  }
  .form__item-input:nth-child(2) {
    margin-bottom: 0;
  }
  .form__item-btn {
    width: 190px;
    height: 37px;
    // background: #6d0000;
    font-size: 15px;
    margin-top: 28px;


  }
  .consultation__text--mobile {
    font-size: 9px;
  }

}


