.footer {

    &__wrap {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  &__item {
    flex: 0 1 770px;
    color: #797975;
    font-size: calc(15px + 15 * (100vw / 1920));
    font-weight: 500;
  }

  &__title {
    font-weight: 700;
    font-size: calc(20px + 20 * (100vw / 1920));
    margin-bottom: calc(30px + 50 * (100vw / 1920));
    // margin-bottom: 80px;
    line-height: 125%;
  }

  &__item-content {
    margin-bottom: calc(30px + 38 * (100vw / 1920));
    // margin-bottom: 68px;
  }

  &__item-text {
    margin-bottom: 35px;
  }

  &__item-link {
    display: block;
    font-family: var(--second-family);
    margin-bottom: 30px;
  }
  &__item-btns {
    display: flex;
    align-items: flex-start;
    gap: 37px;
  }

  &__item-icon {
    width: 78px;
    height: 78px;
    animation: puls 1s infinite;
  }

  iframe {
    width: 100%;
    height: 100%;
  }

  &__map {
    // width: 100%;
    // flex-grow: 1;
    flex: 0 1 950px !important;
    height: calc(420px + 262 * (100vw / 1920)) !important;
    // height: 682px;
  }
}

@keyframes puls {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}


@media(max-width: 768px) {
  .footer__wrap {
    display: block;
  }
  .footer__container {
    padding: 0;
  }
  .footer__item {
    padding: 0 20px;
    margin: 26px 0;
  }
  .footer__map {
    height: calc(300px + 13.6458333333vw) !important;
  }
}

@media(max-width: 540px) {
  .footer__item {
    color: #000;
    font-size: 15px;
    margin: 26px 0;
  }
  .footer__title {
    font-size: 20px;
    margin-bottom: 20px;
  }
  .footer__item-text {
    margin-bottom: 10px;
  }
  .footer__item-link {
    font-weight: 700;
    margin-bottom: 8px;
    margin-top: 21px;
  }
  .footer__item-link:last-child {
    margin-top: 0;
  }
  .footer__item-content {
    margin-bottom: 30px;
    // font-size: 14.7px;
    font-size: 16px;
    letter-spacing: 0.5px;
  }
  .footer__item-text:nth-child(2) {
    letter-spacing: 1px;
  }
  .footer__item-icon {
    width: 40px;
    height: 40px;
  }
  .footer__item-btns {
    gap: 20px;
    align-items: flex-start;
  }
  .footer__map {
    height: 420px !important;
  }
  .header__btn-footer {
    margin: 0;
  }
  .header__btn--desctope {
    display: none;
  }
}
