.about {
  padding: 60px 0 46px;

  &__item {
    display: flex;
    // align-items: center;
    // justify-content: space-between;
    gap: calc(20px + 38 * (100vw / 1920));
    align-items: center;
    &:not(:last-child) {
      margin-bottom: calc(20px + 38 * (100vw / 1920));
      // margin-bottom: 60px;
    }
  }

  &__list-item:not(:last-child) {
    margin-bottom: calc(20px + 25 * (100vw / 1920));
  }
  &__item:first-child img {
    object-position: left;
  }
  &__item:nth-child(even) {
    .about__item-content {
      order: 1;
    }
    .about__item-img {
      order: 2;
    }
  }

  &__item-img {
    flex: 0 1 770px;

  }

  &__item-content {
    // flex: 0 1 733px;
    flex: 0 1 712px;
    // padding: calc(20px + 20 * (100vw / 1920)) 0;
  }

  &__item-text {
    // font-size: calc(15px + 11 * (100vw / 1920));
    span {
      font-weight: 600;
    }

  }
  &__item-text:not(:last-child) {
    margin-bottom: calc(20px + 20 * (100vw / 1920));
  }
}
.about__item-img.mobile {
  display: none;
}

@media(max-width: 1600px) {
  .about__list-item:not(:last-child) {
    margin-bottom: 15px;
  }
  .about__item-text:not(:last-child) {
    margin-bottom: 15px;
  }
}

@media(max-width: 1250px) {
  .about__list-item:not(:last-child) {
    margin-bottom: 15px;
  }
  .about__item-text:not(:last-child) {
    margin-bottom: 15px;
  }
  .about__item-content {
    font-size: 16px;
  }
}

@media(max-width: 900px) {
  .about__item {
    display: block;
  }
  .about__item-content {
    font-size: calc(15px + 0.46875vw);
  }

}

@media(max-width: 540px) {
  .about__item:first-child img {
    object-position: center top;
  }
  .about {
    padding: 21px 0 23px;
  }
  .about__item-text:not(:last-child) {
    margin-bottom: 20px;
  }
  .about__item:first-child {
    display: flex;
    flex-direction: column;
    gap: 12px;
    .about__item-img {
      flex: 0 1 auto;
      order: 2;
      margin: 0 -20px;
      height: 395px;
      width: 100%;
    }
    .about__item-content{
      padding: 0 8px;
      flex: 0 1 auto;
      order: 1;
    }
  }
  .about__item:nth-child(2), .about__item:last-child {
    height: 407px;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    background: #d50000;
    display: flex;
    align-items: center;
    .about__item-img {
      display: none;
    }
    .about__item-content {

      color: #fff;
      padding: 26px 0px;
      font-size: 15px;
    }
    .about__item-text {
      text-align: center;
      width: 90%;
      margin: 0 auto;
    }
  }
  .about__list {
    margin-right: 15px;
  }
  .about__item:not(:last-child) {
    margin-bottom: 26px;
  }
  .about__item:last-child {
    .about__list {
      margin-top: 30px;
    }
    .about__list-item:not(:last-child) {
      margin-bottom: 27px;
    }

  }
  .about__list-item:not(:last-child) {
    margin-bottom: 20px;
}

.about__item-img.mobile {
  display: block;
}
.about__item-img.desctop {
  display: none;
}
}
