html {
  box-sizing: border-box;
  // font-size: 0.8335vw;
  // font-size: .52085vw;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
}

a {
  text-decoration: none;
  color: inherit;
  display: inline-block;
}

h1, h2, h3, h4 {
  padding: 0;
  margin: 0;
}

p {
  padding: 0;
  margin: 0;
}
input::placeholder {
  color: #797975;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: calc(15px + 9 * (100vw / 1920));
  line-height: 125%;
  letter-spacing: 0.03em;
  color: #000;
}

.container {
  margin: 0 auto;
  // padding: 0 calc(28px + 152 * (100vw / 1920));
  padding: 0 180px;
  max-width: 1920px;
  // max-width: var(--container-width);
  width: 100%;
}

.footer__container {
  padding-left: max(180px, (100% - var(--content-width)) / 2);
}

