.header__btn--mobile {
  display: none;
}


.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}



.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}


@media(max-width: 1500px) {
  .container {
    padding: 0 100px;
  }
  .footer__container {
    padding-left: max(100px, (100% - var(--content-width)) / 2);
  }
}

@media(max-width: 1200px) {
  .container {
    padding: 0 20px;
  }
  .footer__container {
    padding-left: max(20px, (100% - var(--content-width)) / 2);
  }
}

@media(max-width: 540px) {
  .btn {
    border-radius: 50px;
    width: 206px;
    height: 37px;
    font-weight: 700;
    font-size: 16px;
  }
  .page__body {
    font-size: 15px;
  }
  .header__btn--mobile {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
  }
  .header__btn--desctop {
    display: none;
  }
}
