.header {
  // calc(28px + 152 * (100vw / 1920))
  background: url("../img/bg-header.jpg") no-repeat center;
  background-size: cover;
  text-align: center;

  height: 100vh;
  display: flex;
  align-items: center;
  // height: 990px;
  height: 100vh;

  &__title {
    font-weight: 800;
    font-size: calc(25px + 48 * (100vw / 1920));
    line-height: 130%;
    letter-spacing: 0.02em;
    text-align: center;
    color: #fff;
    margin-bottom: calc(14px + 55 * (100vw / 1920));
    // margin-bottom: 69px;
  }
}

@media(max-width: 540px) {
  .header__title {
    font-size: 25px;
    line-height: 130%;
    margin-bottom: 14px;
  }
  .header {
    // height: 373px;
    height: 103vw;
    background-position: 40% center;
  }

}


