.certificate {
  &__title {
    font-size: calc(13px + 16 * (100vw / 1920));
    text-align: center;
    margin-bottom: 55px;
    font-weight: 400;
    line-height: 125%;
    span {
      font-weight: 600;
    }
  }

}

// slider
.certificate {
  .swiper-slide.svid-item {
    // height: calc(374px + 158 * (100vw / 1920));
    height: 28vw;
  }

  .swiper.swiper__certificate img {
    border-style: none;
    width: 100%;
    height: 95%;
    object-fit: fill;
    object-position: center;
  }

  .swiper-wrapper.section-wrapper {
    margin-top: 0;
    margin-bottom: 0;
  }
  .slider__main {
    margin-bottom: 65px;
    // margin-bottom: calc(90px + 76px);
    position: relative;
    .swiper-slide {
      height: auto;
    }
  }

  .swiper-pagination-bullet {
    /* width: 31px;
    height: 31px; */
    width: 2.2vw !important;
    height: 2.2vw !important;
    background-color: #b3b3b3;
    /* background-color: #D9D9D9; */
  }
  .certificate__pagination {
    bottom: -3.5vw !important;
  }

  .swiper-pagination-bullet-active {
    background-color: #AAAAA9;
  }
  .swiper-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0 0.6vw;
  }
  .swiper__certificate-wrapper {
    position: relative;
    width: 100%;

  }
  .slider__slider-btn{
    position: absolute;
    z-index: 100;
    border: none;
    /* width: 200px; */
    width: 14vw;
    height: 100%;
    padding: 0;
    margin: 0;
  }
  .slider__slider-btn--prev {
    top: 0;
    left: 0;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.2) 0%, rgba(255, 255, 255, 0.2) 100%);
  }
  .slider__slider-btn--next {
    top: 0;
    right: 0;
    background: linear-gradient(270deg, rgba(0, 0, 0, 0.2) 0%, rgba(255, 255, 255, 0.2) 100%);
  }

}

@media(max-width: 540px) {
  .certificate__title {
    font-size: 13px;
    line-height: 125%;
    margin-bottom: 15px;
  }
  .certificate .slider__main .swiper-slide {
    height: 380px;
  }
  .certificate .swiper.swiper__certificate img {
    object-fit: contain;
  }
  .certificate .swiper-pagination-bullet {
    width: 19px !important;
    height: 19px !important;
  }
  .certificate .certificate__pagination {
    bottom: -49px !important;
  }
  .certificate .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0 4.5px;
  }
  .certificate .slider__main {
    margin-bottom: 80px;
  }
  .certificate .swiper.swiper__certificate img {
    border-style: none;
    width: 100%;
    height: 100%;
  }
}
