.price {
  margin-bottom: 120px;
  // calc(26px + 38 * (100vw / 1920))
  &__wrap {
  }

  &__item {
    padding: 0 83px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #797975;
  }

  &__item:nth-child(2) {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  &__item-name {
    font-size: calc(15px + 13 * (100vw / 1920));
    line-height: 110%;
    letter-spacing: 0.02em;
    color: #797975;
    flex: 0 1 600px;
  }

  &__item-wrap {
    flex: 0 1 600px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: calc(15px + 15 * (100vw / 1920));
    color: #797975;
    line-height: 90%;
  }

  &__item-unit {
    text-align: center;
    flex: 0 1 193px;
  }

  &__item-total {
    text-align: center;
    flex: 0 1 230px;
    font-family: var(--second-family);
    font-weight: 300;
  }
}

.price-head {

  &__title {
    font-weight: 700;
    font-size: calc(20px + 32 * (100vw / 1920));
    line-height: 130%;
    letter-spacing: -0.02em;
    color: #797975;
    text-transform: uppercase;
  }

  &__text {
    font-size: calc(20px + 3 * (100vw / 1920));
    line-height: 100%;
    text-align: center;
    color: #797975;
    flex: 0 1 230px;
    padding-bottom: 12px;
  }
}

@media(max-width: 1200px) {
  .price__item {
    padding: 0 20px;
  }
}

@media(max-width: 768px) {
  .price__item-wrap {
    flex-direction: column;
    flex: 0 0 230px;
    gap: 7px;
  }
  .price__item-unit {
    flex: 0 1 auto;
    order: 2;
  }
  .price__item-total {
    flex: 0 1 auto;
    order: 1;
  }
}

@media(max-width: 540px) {
  .price__item {
    padding: 0px;
    color: #000;
  }
  .price-head__title {
    font-size: 20px;
    color: #000;
    text-transform: none;
  }
  .price-head__text {
    color: #000;
    font-size: 10px;
    padding: 8px 0;
  }
  .price-head__text, .price__item-wrap {
    flex: 0 1 100px;
  }
  .price__item:nth-child(2) {
    padding-top: 19px;
    padding-bottom: 17px;
}
  .price__item-name {
    color: #000;
    font-size: 15px;
    flex: 0 1 198px;
  }
  .price__item-unit {
    font-size: 12px;
    color: #000;
  }
  .price__item-total{
    font-weight: 300;
    font-size: 15px;
    color: #000;
  }
  .price__item-wrap {
    gap: 7px;
  }
  .price {
    margin-bottom: 33px;
  }
}
